<template>
    <li @mouseenter="hander(true)" @mouseleave="!hander()" :style="{background:bgcolor}">
          <label>
            <input type="checkbox" v-model="todo.complete"/>
            <span>{{todo.title}}</span>
          </label>
          <button class="btn btn-danger" v-show="isShow" @click="item">删除</button>
        </li>    
</template>

<script>
import PubSub from 'pubsub-js'
export default {
  props:{
    todo:Object,
    index:Number,
    //deleteItem:Function
  },
  data(){
    return{
      bgcolor:'white',
      isShow:false
    }
  },
  methods:{
    hander(isEnter){
      if(isEnter){
        this.bgcolor='#aaaaaa'
        this.isShow=true
      }else{
        this.bgcolor='white'
        this.isShow=false
      }
    },
    item(){
      
      //1。检查输入合法性
      const {todo,index}=this
      if(window.confirm(`确认是否删除${todo.title}任务`)){
        // this.deleteItem(index)
        //发布消息
        PubSub.publish('deleteItem',index)
      }
      //2.根据输入生成一个todo对象
      //3. 添加到todos
      //4.清楚输入
    }
  }
    
}
</script>

<style>
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  display: none;
  margin-top: 3px;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}
</style>



